import React from 'react';
import './style/index.global.less';
export default function Header(props) {
    const ctx = window.g_ctx;
    const initCfg = ctx.getCfgByKey('init', 'cfg');
    const currentLang = props.toolParams.lang;
    function handleClick(lang) {
        ctx.changeLang(lang);
    }
    return (<div className="header-hongkong">
      <div className="logo" style={{ backgroundImage: 'url(/images/big/hongkong/logo.png)' }}>
        <span className="title">{initCfg.title || '--'}</span>
        <span className="subTitle">{initCfg.subTitle || '--'}</span>
      </div>
      <div className="lang-container">
        {initCfg.defaultLang.lang.map((lang) => {
        const className = currentLang === lang.value ? 'lang choose' : 'lang';
        return (<span className={className} data-key={lang.value} onClick={() => {
            handleClick(lang.value);
        }}>
              {lang.text}
            </span>);
    })}
      </div>
    </div>);
}
